import React from "react";
import { VoicePlayer } from "react-voice-components";

const ListItems = (props) => {
  let listItems = null;
  if (props.selectedWorkout) {
    const items = props.selectedWorkout.items;
    listItems = items.map((item) => {
      return (
        <div
          className={"activity " + (item.isSelected ? "active" : null)}
          key={item.key}
        >
          {item.isSelected ? (
            <VoicePlayer play text={item.exerciseText} />
          ) : null}
          <div className="activity-duration">
            {`${
              item.displayedMinutes < 10
                ? "0" + item.displayedMinutes
                : item.displayedMinutes
            }:${
              item.displayedSeconds < 10
                ? "0" + item.displayedSeconds
                : item.displayedSeconds
            }`}
          </div>

          <div className="activity-position">
            <i
              onClick={() => props.moveItemUp(item.key)}
              className={`angle up icon large ${
                item === items[0] || props.blocking || items.length <= 1
                  ? "disabled"
                  : null
              }`}
            ></i>
            <i
              onClick={() => props.moveItemDown(item.key)}
              className={`angle down icon large ${
                (items.length > 0 && item === items[items.length - 1]) ||
                props.blocking ||
                items.length <= 1
                  ? "disabled"
                  : null
              }`}
            ></i>
          </div>

          <div className="activity-name">
            <input
              type="text"
              maxLength="20"
              value={item.exerciseText}
              onChange={(e) => {
                props.setUpdate(e.target.value, item.key);
              }}
            />
          </div>
          <div className={props.blocking ? "disabled" : null}>
            <button
              className="duplicate"
              onClick={() => {
                props.duplicateItem(item.key);
              }}
            >
              <i className="fa fa-plus"></i>
            </button>
            <button
              className="remove"
              onClick={() => {
                props.deleteItem(item.key);
              }}
            >
              <i className="fa fa-trash"></i>
            </button>
          </div>
        </div>
      );
    });
  }

  return props.selectedWorkout ? (
    <div className="third-row">{listItems}</div>
  ) : null;
};

export default ListItems;
