import React from "react";
import List from "./List";
import Timer from "./Timer";
import Adder from "./Adder";
import Header from "./Header";
import Selector from "./Selector";

// external components import
import _ from "lodash";

// sound file imports
import finishSound from "../sounds/finish.wav";
import countdownSound from "../sounds/countdownBegins.wav";
import lastSecond from "../sounds/lastSecondBeep.wav";
import lastSeconds from "../sounds/lastSecondsBeep.wav";

class App extends React.Component {
  state = {
    modalMessage: "",
    showModal: false,
    startCountdown: true,
    isCounting: false,
    blocking: false,
    playerBlock: false,
    selectedWorkout: null,
    workoutsArray: [],
  };

  // *****************************************************************************

  // modal funcs ******************
  handleOpenModal() {
    this.setState({ showModal: true });
  }
  handleCloseModal() {
    const modalTimer = setTimeout(() => {
      this.setState({ showModal: false, modalMessage: "" });
      clearTimeout(modalTimer);
    }, 2500);
  }

  // select workout
  handleSelectWorkout = (workout) => {
    clearInterval(this.myInterval);
    this.setState(
      {
        selectedWorkout: _.cloneDeep(workout),
        startCountdown: true,
        isCounting: false,
        blocking: false,
        playerBlock: false,
      },
      this.totalTimeCalculation
    );
  };

  // create new workout
  handleCreateNewWorkout = () => {
    const newWorkout = {
      title: "",
      totalDurationInSeconds: 0,
      totalDisplayedDurationInSeconds: 0,
      items: [],
    };
    this.setState({
      selectedWorkout: newWorkout,
    });
  };

  // delete workout from list
  handleDeleteWorkout = (workout) => {
    const newWorkouts = _.cloneDeep(this.state.workoutsArray).filter(
      (w) => w.title !== workout.title
    );
    this.setState({
      workoutsArray: newWorkouts,
    });
  };

  // import workout from json file
  handleImportWorkoutsFromFile = () => {
    const input = document.createElement("input");
    input.type = "file";

    input.onchange = (e) => {
      // getting a hold of the file reference
      const file = e.target.files[0];

      // setting up the reader
      const reader = new FileReader();
      reader.readAsText(file, "UTF-8");

      // here we tell the reader what to do when it's done reading...
      reader.onload = (readerEvent) => {
        let content = readerEvent.target.result; // this is the content!
        content = JSON.parse(content);
        this.setState({
          workoutsArray: content,
        });
      };
    };

    input.click();
  };

  // export workouts to json file
  handleSaveWorkoutsToFile = () => {
    const jsonFile = JSON.stringify(this.state.workoutsArray);
    this.downloadFile(jsonFile, "workouts.json");
  };

  // helper for above
  downloadFile(data, fileName, type = "json") {
    // Create an invisible A element
    const a = document.createElement("a");
    a.style.display = "none";
    document.body.appendChild(a);

    // Set the HREF to a Blob representation of the data to be downloaded
    a.href = window.URL.createObjectURL(new Blob([data], { type }));

    // Use download attribute to set set desired file name
    a.setAttribute("download", fileName);

    // Trigger the download by simulating click
    a.click();

    // Cleanup
    window.URL.revokeObjectURL(a.href);
    document.body.removeChild(a);
  }

  // helper for below
  findWithAttr(array, attr, value) {
    for (var i = 0; i < array.length; i += 1) {
      if (array[i][attr] === value) {
        return i;
      }
    }
    return null;
  }

  handleChangeTitle = (e) => {
    // console.log(e.target.value);
    this.setState({
      selectedWorkout: {
        ...this.state.selectedWorkout,
        title: e.target.value,
      },
    });
  };

  // validation to check if title is not empty string
  _titleValidation = () => {
    if (this.state.selectedWorkout.title.trim() === "") {
      // console.log("title not passed validation");
      this.setState({ modalMessage: "Workout must have a title !" }, () => {
        this.handleOpenModal();
        this.handleCloseModal();
      });
      return false;
    } else {
      // console.log("title passed validation");
      return true;
    }
  };

  // save or overwrite selectedWorkout to workoutsArray
  handleSaveWorkout = () => {
    if (this._titleValidation()) {
      const selectedWorkoutTitle = this.state.selectedWorkout.title;
      const index = this.findWithAttr(
        this.state.workoutsArray,
        "title",
        selectedWorkoutTitle
      );
      const curr = _.cloneDeep(this.state.selectedWorkout);
      const array = _.cloneDeep(this.state.workoutsArray);

      if (index !== null) {
        // replace object
        // console.log("replacing obj");
        array[index] = curr;
        this.setState({
          workoutsArray: array,
        });
      } else {
        // add to end of array
        // console.log("adding new");
        array.push(curr);
        this.setState({
          workoutsArray: array,
        });
      }
    }
  };

  // func to calculate total seconds and minutes for state selectedWorkout
  totalTimeCalculation = () => {
    let totalTimeInSeconds = 0;
    let totalDisplayedTimeInSeconds = 0;
    for (const item of this.state.selectedWorkout.items) {
      totalTimeInSeconds += item.seconds + item.minutes * 60;
      totalDisplayedTimeInSeconds +=
        item.displayedSeconds + item.displayedMinutes * 60;
    }
    // convert to hh:mm:ss format
    // return new Date(totalTimeInSeconds * 1000).toISOString().substr(11, 8);
    this.setState({
      selectedWorkout: {
        ...this.state.selectedWorkout,
        totalDurationInSeconds: totalTimeInSeconds,
        totalDisplayedDurationInSeconds: totalDisplayedTimeInSeconds,
      },
    });
  };

  // add item to selected workout
  handleAddItem = (compState, _helperValidation) => {
    const validation = _helperValidation();
    if (validation) {
      // add new item to items array
      let newItem = compState.currentItem;
      let new_items = [...this.state.selectedWorkout.items, newItem];
      let newWorkout = { ...this.state.selectedWorkout, items: new_items };

      // add to list  -- also update state again using callback
      this.setState(
        {
          selectedWorkout: newWorkout,
        },
        this.totalTimeCalculation
      );
    } else {
      // console.log("Error - Validation did not pass - retry the form");
    }
  };

  // move item up
  moveItemUp = (key) => {
    const index = this.findWithAttr(
      this.state.selectedWorkout.items,
      "key",
      key
    );
    const sameItem = this.state.selectedWorkout.items[index];
    const newItems = this.state.selectedWorkout.items.filter(
      (item) => item.key !== key
    );
    newItems.splice(index - 1, 0, sameItem);
    const newWorkout = { ...this.state.selectedWorkout, items: newItems };
    this.setState({
      selectedWorkout: newWorkout,
    });
  };

  // move item down
  moveItemDown = (key) => {
    const index = this.findWithAttr(
      this.state.selectedWorkout.items,
      "key",
      key
    );
    const sameItem = this.state.selectedWorkout.items[index];
    const newItems = this.state.selectedWorkout.items.filter(
      (item) => item.key !== key
    );
    newItems.splice(index + 1, 0, sameItem);
    const newWorkout = { ...this.state.selectedWorkout, items: newItems };
    this.setState({
      selectedWorkout: newWorkout,
    });
  };

  // duplicate item and add at bottom
  duplicateItem = (key) => {
    let newItems = this.state.selectedWorkout.items;
    newItems.map((item) => {
      if (item.key === key) {
        const dupeItem = { ...item, key: Date.now() };
        // dupeItem.key = Date.now();
        newItems.push(dupeItem);
      }
    });
    const newWorkout = { ...this.state.selectedWorkout, items: newItems };
    this.setState(
      {
        selectedWorkout: newWorkout,
      },
      this.totalTimeCalculation
    );
  };

  // delete item func.
  deleteItem = (key) => {
    const filteredItems = this.state.selectedWorkout.items.filter(
      (item) => item.key !== key
    );
    const newWorkout = { ...this.state.selectedWorkout, items: filteredItems };
    this.setState(
      {
        selectedWorkout: newWorkout,
      },
      this.totalTimeCalculation
    );
  };

  // update item in list func.
  setUpdate = (text, key) => {
    const items = this.state.selectedWorkout.items;
    items.map((item) => {
      if (item.key === key) {
        item.exerciseText = text;
      }
    });
    const newWorkout = { ...this.state.selectedWorkout, items: items };

    this.setState({
      selectedWorkout: newWorkout,
    });
  };

  // helper func for countdown timer individual
  countdownInterval = (arrayIndex, itemsArray) => {
    if (this.state.selectedWorkout.totalDisplayedDurationInSeconds !== 0) {
      this.myInterval = setInterval(() => {
        if (arrayIndex < itemsArray.length) {
          const item = itemsArray[arrayIndex];
          let { displayedSeconds, displayedMinutes } = item;

          if (displayedSeconds > 0) {
            item.displayedSeconds = displayedSeconds - 1;
            item.isCounting = true;
            item.isSelected = true;
            if (displayedMinutes === 0 && displayedSeconds === 1) {
              this.playAudioLastSecond();
            } else if (displayedMinutes === 0 && displayedSeconds < 5) {
              this.playAudioLastSeconds();
            }
            const newWorkout = {
              ...this.state.selectedWorkout,
              items: itemsArray,
            };
            this.setState(
              {
                selectedWorkout: newWorkout,
              },
              this.totalTimeCalculation
            );
          }

          if (displayedSeconds === 0) {
            if (displayedMinutes === 0) {
              item.isCounting = false;
              item.isSelected = false;
              const newWorkout = {
                ...this.state.selectedWorkout,
                items: itemsArray,
              };
              this.setState(
                {
                  selectedWorkout: newWorkout,
                },
                this.totalTimeCalculation
              );
              // this.totalTimeCalculation();
              clearInterval(this.myInterval);
              if (itemsArray[arrayIndex + 1]) {
                this.countdownInterval(arrayIndex + 1, itemsArray);
              } else {
                // code here for when displayed seconds = 0;
                this.setState({
                  isCounting: false,
                  startCountdown: false,
                });
                this.playAudio();
              }
            } else {
              item.displayedMinutes = displayedMinutes - 1;
              item.displayedSeconds = 59;
              item.isCounting = true;
              item.isSelected = true;
              const newWorkout = {
                ...this.state.selectedWorkout,
                items: itemsArray,
              };
              this.setState(
                {
                  selectedWorkout: newWorkout,
                },
                this.totalTimeCalculation
              );
              // this.totalTimeCalculation();
            }
          }
        } else {
          clearInterval(this.myInterval);
        }
      }, 1000);
    }
  };

  // func to start counter for items from Timer component
  startCountdownItem = () => {
    const items_ = this.state.selectedWorkout.items;
    let counter = 0;
    let soundCountdownCounter = 0;

    if (
      !this.state.isCounting &&
      this.state.startCountdown &&
      this.state.selectedWorkout.totalDurationInSeconds !== 0
    ) {
      this.setState(
        {
          playerBlock: true,
          isCounting: true,
        },
        () => {
          const playerBlockTimer = setTimeout(() => {
            this.setState({
              playerBlock: false,
            });
            clearTimeout(playerBlockTimer);
          }, 4000);
        }
      );
      const timeout = setTimeout(() => {
        this.countdownInterval(counter, items_);
      }, 3000);

      const timeBeep = setInterval(() => {
        this.playAudioCountdown();
        soundCountdownCounter++;
        if (soundCountdownCounter === 3) {
          clearInterval(timeBeep);
          clearTimeout(timeout);
        }
      }, 1010);
    } else if (!this.state.isCounting) {
      this.setState(
        {
          isCounting: true,
          startCountdown: false,
        },
        this.countdownInterval(counter, items_)
      );
    }
  };

  // func to stop counter for current item - which also stops display below from dropping
  stopCountdownItem = () => {
    if (this.myInterval) {
      clearInterval(this.myInterval);
      this.setState({
        isCounting: false,
        startCountdown: false,
      });
    }
  };

  // timer function - minutes and seconds - and hours
  resetTimer = () => {
    if (this.myInterval) {
      clearInterval(this.myInterval);

      const items = this.state.selectedWorkout.items;
      for (const item of items) {
        item.displayedSeconds = item.seconds;
        item.displayedMinutes = item.minutes;
        item.isSelected = false;
        item.isCounting = false;
      }
      const newWorkout = {
        ...this.state.selectedWorkout,
        items: items,
      };
      this.setState({
        selectedWorkout: newWorkout,
        isCounting: false,
        startCountdown: true,
      });
      this.totalTimeCalculation();
    }
  };

  // toggle ui blocking
  turnBlockingOn = () => {
    this.setState({ blocking: true });
  };
  turnBlockingOff = () => {
    this.setState({ blocking: false });
  };

  // audio funcs ******************
  playAudio() {
    const audioEl = document.getElementsByClassName("audio-element")[0];
    audioEl.play();
  }
  playAudioCountdown() {
    const audioEl = document.getElementsByClassName("audio-element")[1];
    audioEl.play();
  }
  playAudioLastSeconds() {
    const audioEl = document.getElementsByClassName("audio-element")[2];
    audioEl.play();
  }
  playAudioLastSecond() {
    const audioEl = document.getElementsByClassName("audio-element")[3];
    audioEl.play();
  }

  // *****************************************************************************
  render() {
    return (
      <div className="App">
        {/* Audio divs for loading sounds */}
        <div className="audio-div" style={{ visibility: "hidden" }}>
          <div style={{ visibility: "hidden" }}>
            <audio src={finishSound} className="audio-element"></audio>
          </div>
          <div style={{ visibility: "hidden" }}>
            <audio src={countdownSound} className="audio-element"></audio>
          </div>
          <div style={{ visibility: "hidden" }}>
            <audio src={lastSeconds} className="audio-element"></audio>
          </div>
          <div style={{ visibility: "hidden" }}>
            <audio src={lastSecond} className="audio-element"></audio>
          </div>
        </div>
        <Selector
          workoutsArray={this.state.workoutsArray}
          playerBlock={this.state.playerBlock}
          handleSelectWorkout={this.handleSelectWorkout}
          handleCreateNewWorkout={this.handleCreateNewWorkout}
          handleDeleteWorkout={this.handleDeleteWorkout}
          handleSaveWorkoutsToFile={this.handleSaveWorkoutsToFile}
          handleImportWorkoutsFromFile={this.handleImportWorkoutsFromFile}
        />

        <div
          style={{
            visibility: this.state.selectedWorkout ? "visible" : "hidden",
          }}
          className="container"
        >
          <div
            style={{
              visibility: this.state.showModal ? "visible" : "hidden",
              textAlign: "center",
              color: "red",
            }}
            className="modal"
          >
            {this.state.modalMessage}
          </div>
          <Header
            blocking={this.state.blocking}
            selectedWorkout={this.state.selectedWorkout}
            handleSaveWorkout={this.handleSaveWorkout}
            handleChangeTitle={this.handleChangeTitle}
          />
          <Adder
            blocking={this.state.blocking}
            selectedWorkout={this.state.selectedWorkout}
            handleAddItem={this.handleAddItem}
          />
          <List
            blocking={this.state.blocking}
            startCountdown={this.state.startCountdown}
            selectedWorkout={this.state.selectedWorkout}
            deleteItem={this.deleteItem}
            setUpdate={this.setUpdate}
            duplicateItem={this.duplicateItem}
            moveItemUp={this.moveItemUp}
            moveItemDown={this.moveItemDown}
          />
          <Timer
            isCounting={this.state.isCounting}
            playerBlock={this.state.playerBlock}
            selectedWorkout={this.state.selectedWorkout}
            startCountdownItem={this.startCountdownItem}
            stopCountdownItem={this.stopCountdownItem}
            resetTimer={this.resetTimer}
            turnBlockingOn={this.turnBlockingOn}
            turnBlockingOff={this.turnBlockingOff}
          />
        </div>
      </div>
    );
  }
}

export default App;
