import React from "react";

const Header = (props) => {
  const selectedWorkout = props.selectedWorkout;

  if (selectedWorkout) {
    return (
      <div className="Header">
        <div className="title-workout">
          <h1>
            <input
              placeholder="Enter workout title"
              type="text"
              value={selectedWorkout.title}
              maxLength="20"
              onChange={props.handleChangeTitle}
            />
          </h1>
        </div>

        <div className={props.blocking ? "disabled" : null}>
          <div onClick={props.handleSaveWorkout} className="save-workout">
            Save
          </div>
        </div>
      </div>
    );
  }
  return null;
};

export default Header;
