import React from "react";

class Selector extends React.Component {
  javascriptStuff_2 = () => {
    // toggle open / close class
    document
      .querySelector(".custom-select-wrapper")
      .addEventListener("click", function () {
        this.querySelector(".custom-select").classList.toggle("open");
      });
    // for (const option of document.querySelectorAll(
    //   "body div:not(.App):not(.audio-div):not(.arrow):not(.custom-select-wrapper):not(.custom-select):not(.custom-select__trigger):not(.custom-options):not(.custom-option)"
    // )) {
    //   option.addEventListener("click", () => {
    //     if (
    //       document.querySelector(".custom-select").classList.contains("open")
    //     ) {
    //       document.querySelector(".custom-select").classList.toggle("open");
    //     }
    //   });
    // }
  };

  javascriptStuff = () => {
    // remove selected from all and add to this - also add event listener
    for (const option of document.querySelectorAll(".custom-option")) {
      option.addEventListener("click", function () {
        if (!this.classList.contains("selected")) {
          if (document.querySelectorAll(".selected")) {
            for (const op of document.querySelectorAll(".selected")) {
              op.classList.remove("selected");
            }
          }
          this.classList.add("selected");
          this.closest(".custom-select").querySelector(
            ".custom-select__trigger span"
          ).textContent = this.textContent;
        }
      });
    }
  };

  componentDidMount() {
    this.javascriptStuff();
    this.javascriptStuff_2();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.workoutsArray.length !== this.props.workoutsArray.length) {
      this.javascriptStuff();
    }
  }

  renderListOfWorkouts() {
    return this.props.workoutsArray.map((workout) => {
      return (
        <div key={workout.title} className="workout-item">
          <div
            onClick={() => this.props.handleSelectWorkout(workout)}
            className="custom-option"
          >
            <span data-value={workout.title}>{workout.title}</span>
          </div>
          <div
            onClick={() => this.props.handleDeleteWorkout(workout)}
            className="delete-workout"
          >
            <span>
              <i className="fa fa-trash"></i>
            </span>
          </div>
        </div>
      );
    });
  }

  render() {
    return (
      <div
        className={`custom-select-wrapper ${
          this.props.playerBlock ? ".disabled" : null
        } `}
      >
        <div className="custom-select">
          <div className="custom-select__trigger">
            <span>Pick an option...</span>
            <div className="arrow"></div>
          </div>
          <div className="custom-options">
            {this.renderListOfWorkouts()}
            <span
              onClick={this.props.handleCreateNewWorkout}
              className="custom-option create-item"
              data-value="create-new-workout"
            >
              Create new workout
            </span>
            <span
              onClick={this.props.handleSaveWorkoutsToFile}
              className="custom-option save-to-file"
              data-value="save-workouts-to-file"
            >
              Save workouts to file
            </span>
            <span
              onClick={this.props.handleImportWorkoutsFromFile}
              className="custom-option load-to-file"
              data-value="load-workouts-from-file"
            >
              Load workouts from file
            </span>
          </div>
        </div>
      </div>
    );
  }
}

export default Selector;
